import {Vector3} from 'three'

export const PI = Math.PI
export const TAU = PI * 2
export const axis = {
    x: new Vector3(1, 0, 0),
    y: new Vector3(0, 1, 0),
    z: new Vector3(0, 0, 1),
}

export function forceFloat(v) {
    return isFloat(v) ? v : v.toFixed(1)
}

export function isFloat(x) {
    return !!(x % 1)
}

export function triangle(t) {
    return 4.0 * Math.abs(t - Math.floor(t + 0.75) + 0.25) - 1.0
}

export function map(value, start1, stop1, start2, stop2) {
    return ((value - start1) * (stop2 - start2)) / (stop1 - start1) + start2
}

// Envelope delay rise
export const envDR = `float envDR(float v,float p){return clamp((v-p)/(1.-p),.0,1.);}`

export const rand = `float rand(vec2 x){return fract(sin(dot(x,vec2(12.9898,78.233)))*43758.5453);}`

export function fract(v) {
    return v - Math.floor(v)
}