import {Sketch} from './Sketch'
import {genTokenData, Random} from './ABRandom'
// import { Controller } from "./Controller"
// import { Presets } from "./Presets"
import {Model} from './Model'
import Stats from 'stats.js'

function copyCanvas(sketch, filename) {
    let imgData = sketch.renderer.domElement.toDataURL()
    // console.log(imgData)
    let imgNode = document.createElement('img')
    imgNode.src = imgData
    let link = document.createElement('a')
    link.download = filename
    link.href = imgData
    link.click()
}

// Setup URL Param
const queryString = window.location.search
const urlParams = new URLSearchParams(queryString)
const urlParamObj = {
    hash: urlParams.get('hash'),
}

// Setup Token and Random
const projectNum = 777
let hash = urlParamObj.hash
// hash = '0x4387968b3510c39aa35f9f0e40410fa243ea84f67c6fe90707610faa10530e0c'
const tokenData = genTokenData(projectNum, hash)
console.log('hash: ' + tokenData.hash)
const random = new Random(tokenData)

// Setup Sketch
const targetCanvas = document.querySelector('canvas.webgl')
const sketch = new Sketch(targetCanvas)

const model = new Model(sketch, random)
sketch.model = model
sketch.setup()
sketch.tick()
sketch.drawFrame()
