import {TAU, forceFloat} from '../util'

export class Model {
    constructor(parent, random) {
        this.parent = parent
        this.random = random
        this.createPresets()
        const presetList = ['pixie', 'bands', 'diamond', 'random', 'spacePlaid', 'sylo', 'vertigo']
        this.preset = this.random.choice(presetList)
        // this.preset = 'spacePlaid'

        this.setData()
        this.P.cycleFreq = this.random.int(0, 8) * this.random.negate()
        this.P.cycleFreq2 = this.random.int(0, 3) * this.random.negate()
    }

    makeOr() {
        const n = this.random.int(1, 3)
        return {
            A: n & 1,
            B: (n >> 1) & 1,
        }
    }

    createPresets() {
        const R = this.random
        this.presetFunctions = {
            pixie: () => {
                return {
                    focalFollowDo: true,
                    ripple0Do: true,
                    ripple1Do: true,
                    rippleFreq: R.num(0.005, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    ripple0SpiralDo: R.choice([false, '-', '+']),
                    ripple1SpiralDo: R.choice([false, '-', '+']),
                    stageSides: R.choice([3, 4, 5, 6, 128]),

                    crushDo: R.bool(1 / 4),
                    crushAmount: R.choice([4, 8, 16, 24, 32, 64]),
                    crushAngleAmount: R.choice([32, 48, 64, 96, 128, 256]),
                    crushAngleDo: R.bool(1 / 4),
                    spiritDoCoef: R.num(0.0, 0.5),
                    spiritDoDist: R.num(1, 200),
                    spiritDo: R.bool(1 / 16),
                }
            },
            // TODO: Ramp Animations
            bands: () => {
                return {
                    focalFollowDo: false,
                    ripple0Do: true,
                    ripple1Do: true,
                    rippleFreq: R.num(0.005, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    ripple0SpiralDo: R.choice([false, '-', '+']),
                    ripple1SpiralDo: R.choice([false, '-', '+']),

                    crushDo: R.bool(1 / 4),
                    crushAmount: R.choice([4, 8, 16, 24, 32, 64]),
                    crushAngleAmount: R.choice([32, 48, 64, 96, 128, 256]),
                    crushAngleDo: R.bool(1 / 4),
                }
            },
            diamond: () => {
                let o = {
                    cameraFOV: R.num(45, 80),
                    animation: 'pong',
                    focalAngle: R.dec() * TAU,
                    focalLength: R.num(0, 300),
                    focalZ: R.choice([0, R.num(-300, 300)]),
                    opDiamondDo: true,
                    stageSides: 4,
                    triDistortDo: true,
                }

                // Add ripples
                if (R.bool(1 / 8)) {
                    o = {
                        ...o,
                        ...{
                            ripple0Do: true,
                            ripple1Do: true,
                            rippleFreq: R.num(0.005, 0.0005),
                            rippleWidth: R.num(0.1, 0.9),
                            ripple0SpiralDo: R.choice([false, '-', '+']),
                            ripple1SpiralDo: R.choice([false, '-', '+']),
                            crushDo: R.bool(1 / 4),
                            crushAmount: R.choice([12, 16, 24, 32, 64]),
                        },
                    }
                }
                return o
            },
            random: () => {
                const odds = R.dec()

                return {
                    animation: R.choice(['magician', 'pong']),
                    cameraFOV: 90,
                    cameraSpinDo: R.bool(odds),
                    cameraType: false,
                    crushDo: R.bool(odds),
                    focalAngle: R.dec() * TAU,
                    focalFollowDo: true,
                    focalLength: R.num(0, 300),
                    focalZ: R.choice([0, R.num(-300, 300)]),
                    glass: R.choice(['reflect', 'refract']),
                    normalDo: R.bool(odds),
                    opPlaidDo: R.bool(odds),
                    opType: R.choice(['stripes', 'opPlaidDo']),
                    opX: R.int(-16, 16),
                    opY: R.int(-16, 16),
                    orbRadius: R.num(40, 80),
                    polyPackSides: R.int(3, 6),
                    ripple0Do: R.bool(odds),
                    ripple1Do: R.bool(odds),
                    rippleFreq: R.num(0.05, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    spiralDo: R.bool(odds),
                    spiritDo: R.bool(odds),
                    spiritDoCoef: R.num(0.01, 0.2),
                    spiritDoDist: R.num(1, 200),
                    stageSides: R.choice([3, 4, 5, 6, 128]),
                    triDistortDo: R.bool(odds),
                }
            },
            spacePlaid: () => {
                return {
                    animation: R.choice(['pong', 'magician']),
                    cameraFOV: R.num(60, 120),
                    cameraSpinDo: R.bool(),
                    focalFollowDo: true,
                    opPlaidDo: true,
                    rippleFreq: R.choice([0.05, 0.005, 0.0005]),
                    cycleFreq: 1 * R.negate(),
                }
            },
            steps: () => {
                return {
                    animation: 'pong',
                    crushDo: true,
                    focalAngle: R.dec() * TAU,
                    focalFollowDo: true,
                    focalFollowDo: true,
                    focalLength: R.num(0, 300),
                    focalZ: R.choice([0, R.num(-300, 300)]),
                    normalDo: true,
                    ripple0Do: true,
                    ripple1Do: true,
                    rippleFreq: R.num(0.005, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    triDistortDo: true,
                }
            },
            sylo: () => {
                const o = {
                    rippleFreq: R.num(0.005, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    spiritDo: false,
                    crushDo: R.bool(0.25),
                    crushAngleDo: true,
                    crushAngleAmount: R.choice([32, 48, 64, 96, 128, 256]),
                    angleModDo: R.bool(0.25),
                }
                const m = this.makeOr()
                o.ripple0Do = m.A
                o.ripple1Do = m.B
                o.ripple0SpiralDo = R.choice(['-', '+'])
                o.ripple1SpiralDo = R.choice(['-', '+'])

                if (m.A && m.B) {
                    const n = this.makeOr()
                    o.ripple0SpiralDo = n.A ? R.choice(['-', '+']) : false
                    o.ripple1SpiralDo = n.B ? R.choice(['-', '+']) : false
                }
                console.log(o)
                return o
            },
            vertigo: () => {
                const o = {
                    cameraFOV: R.num(45, 60),
                    opX: R.int(-16, 16),
                    opY: R.int(-16, 16),
                    rippleFreq: R.num(0.005, 0.0005),
                    rippleWidth: R.num(0.1, 0.9),
                    crushAngleDo: R.bool(1 / 16),
                    crushAngleAmount: R.choice([3, 5, 6, 8, 12, 16, 24, 32, 64, 128]),
                    angleModDo: R.bool(1 / 16),
                    ripple0Do: true,
                    ripple1Do: true,
                    stripe0Do: true,
                    stripe1Do: R.bool(1 / 16),
                    spiritDo: R.bool(1 / 16),
                    stageSides: R.choice([3, 4, 6, 8, 12]),
                }
                return o
            },
            test: () => {
                return {
                    animation: 'none',
                    ripple0Do: 0,
                    ripple1Do: 0,
                    spiritDo: 1,
                    crushDo: 0,
                    crushAngleDo: 0,
                    sineLinesDo: 0,
                }
            },
        }
    }

    // Populate default data
    setData() {
        const R = this.random
        console.log(this.random)
        this.P = {
            angleModDo: false,
            animation: 'none',
            cameraFOV: 90,
            cameraSpinDo: false,
            cameraType: false,
            crushAmount: 16,
            crushAngleAmount: 2,
            crushAngleDo: false,
            crushDo: false,
            cycleFreq: 0,
            cycleFreq2: 0,
            dutchDo: true,
            focalAngle: 0,
            focalFollowDo: false,
            focalLength: 0,
            focalZ: 0,
            gridLinesDo: R.bool(1/8),
            // gridLinesDo: true,
            normalDo: false,
            normalMod: 1,
            pixRotateDo: false,
            opDiamondDo: false,
            opPlaidDo: false,
            opType: 'none',
            opX: 0,
            opY: 0,
            orbRadius: null,
            polyPackSides: null,
            ripple0Do: false,
            ripple0SpiralDo: '+',
            ripple1Do: false,
            ripple1SpiralDo: '+',
            rippleFreq: 0.0005,
            rippleWidth: 0.9,
            sineLinesDo: false,
            spiralDo: false,
            spiritDo: false,
            spiritDoCoef: 0.02,
            spiritDoDist: 1,
            stageSides: 3,
            stripe0Do: false,
            stripe1Do: false,
            tableOffsets: {x: forceFloat(1), y: forceFloat(1)},
            trianglationsDo: false,
            triDistortDo: false,
        }

        this.P = {...this.P, ...this.presetFunctions[this.preset]()}
    }
}
