import {envDR, rand, forceFloat} from '../util'

export function createSmudgeFragShader(sketch) {
    const R = sketch.random
    const n0 = forceFloat(R.num(0.2, 1.0))
    const n1 = forceFloat(R.num(1.0, 10.0))

  const template = `
#ifdef GL_ES
precision mediump float;
#endif

varying vec3 pixPosition;
uniform vec2 u_resolution;
uniform vec3 u_random;

vec3 random3(vec3 c) {
  // https://thebookofshaders.com/edit.php#11/iching-03.frag
  float j = 4096.0*sin(dot(c,vec3(17.0, 59.4, 15.0)));
  vec3 r;
  r.z = fract(512.0*j);
  j *= .125;
  r.x = fract(512.0*j);
  j *= .125;
  r.y = fract(512.0*j);
  return r-0.5;
}

const float F3 =  0.3333333;
const float G3 =  0.1666667;
float snoise(vec3 p) {
  // https://thebookofshaders.com/edit.php#11/iching-03.frag

  vec3 s = floor(p + dot(p, vec3(F3)));
  vec3 x = p - s + dot(s, vec3(G3));

  vec3 e = step(vec3(0.0), x - x.yzx);
  vec3 i1 = e*(1.0 - e.zxy);
  vec3 i2 = 1.0 - e.zxy*(1.0 - e);

  vec3 x1 = x - i1 + G3;
  vec3 x2 = x - i2 + 2.0*G3;
  vec3 x3 = x - 1.0 + 3.0*G3;

  vec4 w, d;

  w.x = dot(x, x);
  w.y = dot(x1, x1);
  w.z = dot(x2, x2);
  w.w = dot(x3, x3);

  w = max(0.6 - w, 0.0);

  d.x = dot(random3(s), x);
  d.y = dot(random3(s + i1), x1);
  d.z = dot(random3(s + i2), x2);
  d.w = dot(random3(s + 1.0), x3);

  w *= w;
  w *= w;
  d *= w;
  return dot(d, vec4(52.0));
}

${rand}
${envDR}

void main() {
  vec3 st = gl_FragCoord.xyz;
  st.xy /= u_resolution;
  st.x = ((st.x - 0.5) * (u_resolution.x / u_resolution.y)) + 0.5;  // Correct for aspect
  st += u_random.xyz * 2.0;

  // Noise
  float n = rand(st.xy);
  n = rand(st.xy * ${n0} + n * ${n1});  // 0.2 - 1, 1 - 30
  n = envDR(n, 0.373);

  float sn = snoise(st * 1.0) ;
  vec3 sn2 = vec3(st * (st.x * 0.01 + 0.1) * 100.0);  // 100 - 200
  sn = snoise(sn2 + sn * 4.0);  // 0.0 - 4.0
  sn = envDR(sn, 0.4);  // 0.5 - 0.8
  gl_FragColor = vec4(sn, sn, sn, 1.0);  // Main
}`
    return template
}
