import * as T from 'three'
import {PI, TAU, axis, forceFloat, triangle, map, fract} from '../util'

export function createOrbAnimation(S, material, geometry) {
    const R = S.random
        const mesh = new T.Mesh(geometry, material)
        const size = 100
        mesh.scale.set(size, size, size)
        mesh.position.z = -200
        S.scene.add(mesh)

    return () => { }
}

export function createSpiralAnimation(S, material, geometry) {
    const R = S.random
    const path = new T.Path()

    // Make Curve
    const nPoints = S.model.P.stageSides
    const vList = []
    const center = new T.Vector2(0.0, 0.0)
    for (let i = 0; i < nPoints; i++) {
        const v = new T.Vector2(1.0, 0.0)
        v.rotateAround(center, (i / nPoints) * TAU + S.roomRotY)
        vList.push(v)
    }
    path.setFromPoints(vList)
    path.closePath()

    const v = path.getPoint(0.1)
    const animations = []

    const nShapes = R.choice([16, 32, 64, 96, 128])
    const dur = R.choice([45000, 60000, 90000, 120000, 240000])
    const nLoops = R.num(1, 8)
    const size = R.num(20, 80)
    const radius = 220 + (R.num(0, size) * 0.5)
    const direction = R.negate()

    // const radius = 220
    for (let i = 0; i < nShapes; i++) {
        // const dur = R.num(10000, 60000)
        // const dur = 30000 * R.choice([1, 2, 3])
        // const dur = 90000
        const mesh = new T.Mesh(geometry, material)
        mesh.scale.set(size, size, size)
        if (S.model.P.stageSides == 4) {
            mesh.rotateOnAxis(axis.z, S.roomRotY + TAU / 8)
        }

        S.scene.add(mesh)
        const z = map(i, 0, nShapes - 1, 100, -S.roomLength / 2)

        animations.push(() => {
            const t = (Date.now() % dur) / dur * direction
            const offset = (i / nShapes) * nLoops
            const v = path.getPoint(fract(t + offset))
            mesh.position.x = v.x * radius
            mesh.position.y = v.y * radius
            mesh.position.z = z

            // Rotate
            // const a = Math.atan2(mesh.position.y, mesh.position.x)
            // mesh.setRotationFromAxisAngle(axis.z, a + TAU / 8)
        })
    }

    return () => {
        for (const a of animations) {
            a()
        }
    }
}

export function createPongOrb(S, material, geometry) {
    const R = S.random
    const mesh = new T.Mesh(geometry, material)
    const mesh2Size = R.num(80, 160)
    mesh.scale.set(mesh2Size, mesh2Size, mesh2Size)
    S.scene.add(mesh)

    const pongOffset = new T.Vector3(R.num(-1, 1), R.num(-1, 1), R.num(-1, 1))
    pongOffset.normalize()
    const sizeOfSpace = R.num(100, 200)

    return () => {
        const thisPeriod = 60000
        const phase = (Date.now() % thisPeriod) / thisPeriod
        mesh.position.x = triangle(phase + pongOffset.x) * sizeOfSpace
        mesh.position.y = triangle(phase + pongOffset.y) * sizeOfSpace
        mesh.position.z = triangle(phase + pongOffset.z) * sizeOfSpace * 2.0 - 350

        for (const c of 'xyz') {
            S.uniformFocal[c] = mesh.position[c]
        }
    }
}

export function createAnimationMagician(S, material) {
    const R = S.random
    const P = S.model.P

    const geometry = S.shapes[R.choice(['sphere', 'box', 'ring'])]
    const nShapes = 21
    const animations = []
    const pongOffset = new T.Vector3(R.num(-1, 1), R.num(-1, 1), R.num(-1, 1))
    pongOffset.normalize()

    const f0 = R.int(-5, 5)
    const f1 = R.int(-5, 5)

    for (let i = 0; i < nShapes; i++) {
        // const mesh = new THREE.Mesh(R.choice([geometry, geometryBox]), material)
        const mesh = new T.Mesh(geometry, material)
        mesh.position.z = R.num(-150, -200)
        // const scale = R.num(8, 40)
        // const v = i
        const v = Math.sin((i / nShapes + 0.5) * TAU * 4) * 0.5 + 0.5
        const scale = map(v, 0, 1, 8, 64)
        mesh.scale.set(scale, scale, scale)
        S.scene.add(mesh)

        // const thisPeriod = R.int(45000, 60000)
        // const thisPeriod = 60000

        const index = i
        // const offset = R.dec()
        const offset = (i / nShapes) * 1.0
        const thisPeriod = 60000 * 1.0
        const thisPeriod2 = 60000 * 0.75

        animations.push(() => {
            // const sizeOfSpace = (Math.sin(Date.now() / 3000 + offset * TAU) + 1) * 0.5 * 150
            const sizeOfSpace = 100
            const phase = (Date.now() % thisPeriod) / thisPeriod
            const phase2 = (Date.now() % thisPeriod2) / thisPeriod2
            const tOffset = offset
            mesh.position.x = Math.cos(f0 * (tOffset + phase) * TAU) * sizeOfSpace * 1
            mesh.position.y = Math.sin(f1 * (tOffset + phase) * TAU) * sizeOfSpace * 1
            mesh.position.z = triangle(tOffset + phase2 + pongOffset.z) * sizeOfSpace * 2.0 - 250
            // mesh.setRotationFromAxisAngle(axis.x, Date.now() / 1300 + (offset + phase) * TAU)
            // mesh.setRotationFromAxisAngle(axis.y, Date.now() / 1000 + (offset + phase) * TAU)
        })
    }

    return () => {
        for (const a of animations) {
            a()
        }
    }
}
