import {envDR, rand, forceFloat} from '../util'

export const vertexBasicShader = `void main() {
    gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1.0);
}`

export const fragTextureShader = `#ifdef GL_ES
precision mediump float;
#endif

uniform vec2 u_resolution;
uniform sampler2D u_texture;

void main() {
  vec3 st = gl_FragCoord.xyz;
  st.xy /= u_resolution;
  gl_FragColor = texture(u_texture, st.xy);
}`

export const vertexInvisiShader = `varying vec3 pixPosition; 
varying vec4 worldPosition;
varying vec3 vNormal;
varying vec3 view;

void main() {
    pixPosition = position;
    vNormal = normal;    
    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    worldPosition = modelMatrix * vec4(position, 1.0);
  }`

export const vertexInvisiShapesShader = `varying vec3 pixPosition; 
varying vec4 worldPosition;
varying vec3 vNormal;
varying vec3 view;

const float TAU = 6.283185307179586;
uniform float u_now;

void main() {
    vNormal = normal;    
    vec3 pos = position;
    mat4 mvm = modelViewMatrix;
    worldPosition = mvm * vec4(pos, 1.0);

    // Modulation
    float c = 0.3;
    float f = 0.1;
    float m = -u_now / 60000.0 * TAU * 10.0;
    // pos.x += sin(pos.x + m + worldPosition.y * f) * c;
    // pos.z += cos(pos.x + m + worldPosition.x * f) * c;
    // pos.y += cos(m + worldPosition.x * f) * c;

    pixPosition = pos;
    worldPosition = mvm * vec4(pos, 1.0);
    vec4 nWorldView = worldPosition /= worldPosition[3];
    view = normalize(-nWorldView.xyz);
    gl_Position = projectionMatrix * worldPosition;

    worldPosition = modelMatrix * vec4(position, 1.0);  // IMPORTANT: Keeps textures aligned
  }`

export function addViewCode(code, R) {
    code = code.replace(
        'VIEWDECLARATION',
        `// VIEWDECLARATION
  varying vec3 view;
  `,
    )

    let c = R.weighted([80, '', 19, 'o.xyz*=dot(view,vNormal);', 1, 'o.xyz*=1.0-dot(view,vNormal);'])
    code = code.replace(
        'VIEWCODE',
        `// VIEWCODE
        ${c}`,
    )
    return code
}

export function generateShader(P, R, isShape = false) {
    let ripple0Density = R.num(0.001, 0.005)
    const phases = [0.0, 0.25, 0.5, 0.75]
    const opPhase = R.choice(phases).toFixed(2)
    const opBand = R.choice([1 / 3, 0.5])
    const opPlaidDoPhase = R.choice(phases).toFixed(2)
    const phaseStart = R.dec().toFixed(4)
    const phaseStart2 = R.dec().toFixed(4)
    const opPlaidDoDiv = R.choice([0.02, 0.01, 0.005])
    const opPlaidDoAmp = R.dec().toFixed(1)

    // OpArt
    let stripeWeight = R.num(0.001, 0.1)
    stripeWeight = 0.001
    // let stripeWeight = '(cos(phase * TAU) + 1.0) * 0.5'  // TODO: Explore this

    // Collect shader code
    let code = []

    // Rotate Pix
    if (P.pixRotateDo) {
        code.push(`pix.xz = rotate2d(u_phase2 * TAU) * pix.xy;  // PixRotate`)
    }

    // Angle Modulation
    if (P.angleModDo) {
        const amp = R.num(0, 0.1).toFixed(2)
        code.push(`focalAngle += sin(focalPixDistance + u_phase * 32.0 * TAU) * ${amp};  // AngleMod`)
    }

    // Radial Angle Crush
    if (P.crushAngleDo) {
        const f = (R.choice([0, 1]) * R.negate()).toFixed(1)
        code.push(`focalAngle = crush(focalAngle + u_phase * ${f}, ${P.crushAngleAmount.toFixed(1)});  // CrushAngle`)
    }

    // Ripples
    // Produces a choice of ['', '+ focalAngle', '- focalAngle']
    const rippleSpiral0 = P.ripple0SpiralDo ? ` ${P.ripple0SpiralDo} focalAngle` : ''
    const rippleSpiral1 = P.ripple1SpiralDo ? ` ${P.ripple1SpiralDo} focalAngle` : ''
    if (P.ripple0Do)
        code.push(
            `t.x += envDR(fract(focalPixDistance * ${ripple0Density} - phase${rippleSpiral0}), ${P.rippleWidth}); // ripple 0`,
        )
    if (P.ripple1Do)
        code.push(
            `t.x += envDR(fract(focalPixDistance * ${P.rippleFreq} + phase${rippleSpiral1}), u_rippleWidth);  // ripple 1`,
        )

    // Release that worked
    if (P.trianglationsDo) {
        let n1 = (R.int(1, 4) * R.negate()).toFixed(1)
        let n2 = (R.int(1, 4) * R.negate()).toFixed(1)
        let n3 = R.int(-4, 4).toFixed(1)
        let n4 = R.int(-4, 4).toFixed(1)
        code.push(
            `t.x += + (triangle((u_phase * ${n3} + focalPixDistance * 0.002 + focalAngle * 1.0) * TAU * ${n1}) * 0.5 + 0.5);`,
        )
        code.push(
            `t.x += + (triangle((u_phase * ${n4} + focalPixDistance * 0.002 + focalAngle * -1.0) * TAU * ${n2}) * 0.5 + 0.5);`,
        )
    }

    // Crush
    if (P.crushDo)
        code.push(`t.x = floor(fract(t.x) * ${forceFloat(P.crushAmount)}) / ${forceFloat(P.crushAmount)}; // crush`)

    // Stripes
    const pixList = [2.0, 1.0, 0.5, 0.25]
    const pixCoef0 = 0.002 * R.choice(pixList) * R.negate()
    const pixCoef1 = pixCoef0 * R.choice(pixList) * R.negate()
    code.push(`float pixCoef0 = ${pixCoef0};`)
    code.push(`float pixCoef1 = ${pixCoef1};`)
    // if (P.stripe0Do) code.push(`t.x += step(${opBand}, fract(pix.y * ${stripeWeight} + pix.x * pixCoef0  * u_opArt.x + pix.z * pixCoef0  * u_opArt.y + phase + ${opPhase})) * fract(phase + ${opPhase});  // stripes0`)
    // if (P.stripe1Do) code.push(`t.x += step((sin(phase * TAU) + 1.0) * 0.5, fract(pix.y * ${stripeWeight} + pix.x * pixCoef1  * u_opArt.x + pix.z * pixCoef1 * u_opArt.y + phase + ${opPhase})) * fract(phase + ${opPhase});  // stripes1`)
    if (P.stripe0Do)
        code.push(
            `t.x += step(${opBand}, fract(zDepth * ${stripeWeight} + pix.x * pixCoef0  * u_opArt.x + zDepth * pixCoef0  * u_opArt.y + phase + ${opPhase})) * fract(phase + ${opPhase});  // stripes0`,
        )
    if (P.stripe1Do)
        code.push(
            `t.x += step((sin(phase * TAU) + 1.0) * 0.5, fract(pix.y * ${stripeWeight} + pix.x * pixCoef1  * u_opArt.x + zDepth * pixCoef1 * u_opArt.y + phase + ${opPhase})) * fract(phase + ${opPhase});  // stripes1`,
        )

    // if (P.spiritDo) code.push('t.x += sin((focalPixDistance / 256.0) * u_spiritDoDist) * cos(pix.y) * u_spiritDoCoef + t.x;  // spiritDo')

    if (P.spiritDo) {
        code.push('t.x += sin((focalPixDistance / 256.0) * u_spiritDoDist) * cos(pix.y) * u_spiritDoCoef;  // spiritDo')
    }


    // if (P.spiritDo)
    //     code.push('t.x += sin((focalPixDistance / 256.0) * u_spiritDoDist) * cos(pix.y) * u_spiritDoCoef;  // spiritDo')

    // if (P.opPlaidDo) code.push(`t.x += dot(step(0.5, fract(pix * ${opPlaidDoDiv})), vec3(sin((phase + ${opPlaidDoPhase}) * TAU) * ${opPlaidDoAmp}));  // opPlaidDo`)
    if (P.opPlaidDo) {
        const f = R.choice([-1, 0, 1]).toFixed(1)
        code.push(`
      vec3 pix2 = pix; 
      pix2.xz = rotate2d(u_phase2 * ${f} * TAU) * pix.xy;
      t.x += dot(step(0.5, fract(pix2 * ${opPlaidDoDiv})), vec3(sin((phase + ${opPlaidDoPhase}) * TAU) * ${opPlaidDoAmp}));  // opPlaidDo`)
    }

    // Diamond
    if (P.opDiamondDo) {
        const diamondFreq = 1 / R.choice([24.0, 32.0, 48.0])
        const diamondFreq2 = diamondFreq // TODO: Multiply b 5
        const diamondFreq3 = diamondFreq
        const diamondAmp = R.choice([0.25, 0.5])
        const waveType = R.choice(['triangle', 'sin'])
        code.push(`float diamondAmp = ${forceFloat(diamondAmp)};
      t.x += ${waveType}(pix.x * ${diamondFreq2} * TAU) * diamondAmp + ${waveType}((zDepth * 0.05) * ${diamondFreq} * TAU) * diamondAmp + ${waveType}((pix.y + 0.25) * ${diamondFreq3} * TAU) * diamondAmp; 
  `)
    }

    if (P.normalDo) code.push(`t.x += abs(vNormal.z) * ${P.normalMod.toFixed(1)};  // Normal mod`)

    if (P.triDistortDo) {
        const tdAmt = R.num(0.05, 0.3)
        code.push(`t.x += (triangle(t.x * TAU) * 2.0 - 1.0) * ${tdAmt};  // TriDistort`)
    }

    // Sine lines
    if (P.sineLinesDo) code.push(`t.x += sin((1.0 - fract(pix.y * 0.01)) * TAU * 1.0) * 0.5 + 0.5;  // Sine Lines`)

    // zDepth
    // TODO: parameterize zDepth * COEF
    //                    (1 - )
    if (true) code.push(`t.x += sin((1.0 - fract(zDepth * 0.001)) * TAU * 1.0) * 0.5 + 0.5;  // Sine Lines`)

    // TODO: Implement this as part of opArt
    if (P.gridLinesDo) {
        const r = R.num(0.005, 0.025)
        code.push(`
    float sAmt = ${forceFloat(r)};
    vec3 pixGrid = pix;
    // pixGrid.xy = rotate2d(u_phase * -TAU) * pixGrid.xy;
    t.x += envDR(fract((pixGrid.y * sAmt)), 0.9);
    t.x += envDR(fract((pixGrid.x * sAmt)), 0.9);
    t.x += envDR(fract((pixGrid.z * sAmt)), 0.9);
  `)
    }

    // Radial Mixing
    if (true)
        code.push(`
    float radialMix = sin(focalAngle * TAU) * cos(focalAngle * TAU) * 0.1;
    float distFromCenter = distance(worldPosition.xy, st.xy) * 0.05;
    radialMix *= clamp(distFromCenter - 10.0, 0.0, 1.0);
    t.x += radialMix * 2.0;
  `)

    // DEV
    // if (true) code.push(`t.x += ((phase + focalAngle) * 4.0) * 1.5;`)
    // if (true) code.push(`t.x += focalAngle + sin((phase + focalAngle) * TAU * 3.0) * 1.0;`)

    code = code.join('\n')

    const viewDeclaration = isShape ? 'varying vec3 view;' : ''
    const viewCode = `
  asdf
  `
    const viewCodeRendered = isShape ? viewCode : ''

    const template = `
#ifdef GL_ES
precision mediump float;
#endif

varying vec3 pixPosition;
varying vec4 worldPosition;
varying vec3 vNormal;
//VIEWDECLARATION

const float TAU = 6.283185307179586;
const float PI = 3.141592653589793;

uniform float u_now;
uniform vec2 u_resolution;
uniform float u_phase;
uniform float u_phase2;
uniform vec3 u_focal;
uniform float u_rippleWidth;
uniform sampler2D u_texture;
uniform sampler2D u_smudge;
uniform vec2 u_opArt;
uniform float u_spiritDoDist;
uniform float u_spiritDoCoef;
uniform float u_cycleFreq;
uniform vec3 u_meshPos;

${rand}
${envDR}

float triangle(float t) {
  t /= TAU;
  return 4.0 * abs(t - floor(t + 0.75) + 0.25) - 1.0;
}

float crush(float v, float amt) {
  return floor(fract(v) * amt) / amt;
}

mat2 rotate2d(float a) {
  return mat2(cos(a), -sin(a), sin(a), cos(a));
}

float map(float value, float start1, float stop1, float start2, float stop2) {
  return (value - start1) * (stop2 - start2) / (stop1 - start1) + start2;
}

float ns(float p,vec2 v) {return rand(vec2(sin(p*TAU)*0.01+v.x,cos(p*TAU)*0.01+v.y))*${R.num(0.005, 0.05)};}

void main() {
  vec3 st = gl_FragCoord.xyz;
  st.xy /= u_resolution;
  vec3 stcopy = st;
  st.x = ((st.x - 0.5) * (u_resolution.x / u_resolution.y)) + 0.5;  // Correct for aspect
  
  // Globals
  float zDepth = gl_FragCoord.z / gl_FragCoord.w;
  vec3 pix = pixPosition;
  pix = worldPosition.xyz;
  float phase = u_phase;
  vec2 t = vec2(${phaseStart}, ${phaseStart2});
  float focalPixDistance = distance(u_focal, pix);
  float focalAngle = atan(pix.y - u_focal.y, pix.x - u_focal.x) / TAU;

  ${code}

  // Palette Cycle
  t.x += phase * ${P.cycleFreq.toFixed(1)};
  t.y += phase * ${P.cycleFreq2.toFixed(1)};

  // TODO: Implement
  t.x += envDR(fract(phase * 2.0), 0.1) * 2.0 * ${R.negate()}.0;
  // t.x += envDR(fract(phase * 1.0), 0.1) * 1.0 * ${R.negate()}.0;
  // t.x += envDR(fract(phase), 0.5) * u_cycleFreq;

  // Set noise
  t.x += ns(phase, st.xy);
  t.y += ns((0.25+phase), st.xy);

  // Set smudge
  vec4 smudge = texture(u_smudge, fract(stcopy.xy * 0.5));
  t.x += smudge.x * 0.1;
  t.y += smudge.y * 0.1;


  // Set Depth Palette Modulation
  float dfc = distance(vec2(0.5), st.xy);
  vec4 o = texture(u_texture, vec2(fract(t.x + ${P.tableOffsets.x}), fract(${
        P.tableOffsets.y
    } + -phase + dfc * 0.125)));

  
  // vec4 o = texture(u_texture, vec2(fract(t.x + ${P.tableOffsets.x}), fract(n2 + ${P.tableOffsets.y})));


  // VIEWCODE


  // Set Vignette
  vec3 st3 = gl_FragCoord.xyz;
  st3.xy /= u_resolution;
  float vDistance = distance(vec2(1.0), st3.xy);
  vec3 cream = vec3(1.0, 0.95, 0.9);
  vec3 black = vec3(0.0);
  vec3 vignetteColor = black;
  o.rgb = mix(o.rgb, vignetteColor, clamp(vDistance * 2.0 -2.0, 0.0, 0.8));

  // Inverrse
  // o = clamp(o, 0.0, 1.0);
  // o.rgb = 1.0 - o.rgb;

  // Set Color Levels
//   o *= 1.1;
  o += 0.02;
  o *= 1.1;
  o.r = o.r * 1.2 + 0.1 * stcopy.y;
  o.g = o.g * 1.0 + 0.0;
  o.b = o.b * 1.05 + 0.1 * stcopy.y;



  gl_FragColor = o;  // Main
  //  gl_FragColor = texture(u_smudge, fract(stcopy.xy));  // Debug Smudge
  // gl_FragColor = texture(u_texture, fract(stcopy.xy * 0.5));  // Gradient
}`

    return template
}
