import * as T from 'three'
import {Sizes} from './Sizes'
import {addViewCode, generateShader, vertexInvisiShader, vertexInvisiShapesShader} from './Shader'
import {createPaletteGridTexture} from './PaletteGridTexture'
import {PI, TAU, axis, forceFloat, triangle} from '../util'
import {
    createSpiralAnimation,
    createTestAnimation,
    createPongOrb,
    createAnimationWavyRings,
    createAnimationMagician,
    createOrbAnimation,
} from './Animations'
import {createSmudgeTexture} from './SmudgeTexture'

export class Sketch {
    constructor(targetCanvas, model) {
        this.canvas = targetCanvas
        this.sizes = new Sizes(this)
    }

    setup() {
        this.random = this.model.random
        // selfiePrint(this.random)
        const R = this.random
        let P = this.model.P

        this.renderer = new T.WebGLRenderer({
            antialias: true,
            canvas: this.canvas,
            powerPreference: 'high-performance',
            preserveDrawingBuffer: true,
        })

        // Scene
        this.scene = new T.Scene()

        // Set Camera
        this.camera = new T.PerspectiveCamera(P.cameraFOV, 1, 0.1, 10000)
        this.scene.add(this.camera)

        // Update size of sketch
        this.sizes.updateAll()

        // Set Materials
        const paletteGridTexture = createPaletteGridTexture(this)
        const smudgeTexture = createSmudgeTexture(this)
        this.renderer.setSize(this.sizes.width, this.sizes.height)

        // Set Uniforms
        this.uniformMeshPos = new T.Vector3(0, 0, 0)
        this.uniformFocal = new T.Vector3(P.focalLength, 0, P.focalZ)
        this.uniformFocal.applyAxisAngle(axis.z, P.focalAngle)

        this.uniforms = {
            u_now: {value: 0.0},
            u_resolution: {value: {x: this.sizes.width, y: this.sizes.height}},
            u_phase: {value: 0.0},
            u_phase2: {value: 0.0},
            u_focal: {value: this.uniformFocal},
            u_texture: {type: 't', value: paletteGridTexture},
            u_smudge: {type: 't', value: smudgeTexture},
            u_rippleWidth: {value: P.rippleWidth},
            u_opArt: {value: {x: P.opX, y: P.opY}},
            u_spiritDoDist: {value: P.spiritDoDist},
            u_spiritDoCoef: {value: P.spiritDoCoef},
            u_cycleFreq: {value: P.cycleFreq},
            u_meshPos: {value: this.uniformMeshPos},
        }

        // this.uniforms2 = {
        //     u_now: {value: 0.0},
        //     u_resolution: {value: {x: this.sizes.width, y: this.sizes.height}},
        //     u_phase: {value: 0.0},
        //     u_phase2: {value: 0.0},
        //     u_focal: {value: this.uniformFocal},
        //     u_texture: {type: 't', value: paletteGridTexture},
        //     u_smudge: {type: 't', value: smudgeTexture},
        //     u_rippleWidth: {value: P.rippleWidth},
        //     u_opArt: {value: {x: P.opX, y: P.opY}},
        //     u_spiritDoDist: {value: P.spiritDoDist},
        //     u_spiritDoCoef: {value: P.spiritDoCoef},
        //     u_cycleFreq: {value: P.cycleFreq},
        //     u_meshPos: {value: this.uniformMeshPos},
        // }

        const uniforms = this.uniforms
        // const uniforms2 = this.uniforms2
        const invisiSharedFragShader = generateShader(P, R)
        const invisiShaperVersion = addViewCode(invisiSharedFragShader, R)

        const invisiMaterial = new T.ShaderMaterial({
            vertexShader: vertexInvisiShader,
            fragmentShader: invisiSharedFragShader,
            uniforms,
        })

        // const invisiShapesMaterial = invisiMaterial
        const invisiShapesMaterial = new T.ShaderMaterial({
            vertexShader: vertexInvisiShapesShader,
            fragmentShader: invisiShaperVersion,
            side: T.DoubleSide,
            uniforms,
        })

        // Create room
        let doRotateY = R.bool()
        // this.roomLength = R.num(800, 2000)
        this.roomLength = 2000
        // const dim = R.bool() ? {x: 300, y: 150} : {x: 220, y: 220}
        const dim = {x: 220, y: 220}
        const geometry = new T.CylinderGeometry(dim.x, dim.y, this.roomLength, P.stageSides)
        const mesh = new T.Mesh(geometry, invisiMaterial)
        mesh.material.side = T.DoubleSide
        mesh.rotation.x = TAU / 4

        if (doRotateY) {
            // this.roomRotY +=
            mesh.rotation.y = TAU / (P.stageSides * 2) + TAU / P.stageSides
        }

        if (P.dutchDo) {
            // this.roomRotY += TAU / (P.stageSides * 2) + TAU / P.stageSides
            mesh.rotation.y += (TAU / 12) * R.negate()
        }
        this.roomRotY = mesh.rotation.y - TAU / 4

        this.scene.add(mesh)

        // Create shapes
        this.shapes = {
            sphere: new T.SphereGeometry(0.5, 64, 32),
            box: new T.BoxGeometry(1, 1, 1),
            ring: new T.TorusGeometry(0.5, 0.05, 128, 128),
        }

        // Set Animations
        this.animations = []

        const animationList = {
            orb: () => {
                return createOrbAnimation(this, invisiShapesMaterial, this.shapes.sphere)
            },
            pong: () => {
                const geo = this.shapes[R.choice(['sphere', 'box', 'ring'])]
                return createPongOrb(this, invisiShapesMaterial, geo)
            },
            spiral: () => {
                const shape = R.weighted([3, 'box', 1, 'sphere'])
                return createSpiralAnimation(this, invisiShapesMaterial, this.shapes[shape])
            },
            magician: () => {
                return createAnimationMagician(this, invisiShapesMaterial)
            },
            none: () => {
                return () => {}
            },
        }

        const aniKeys = Object.keys(animationList)
        const keyChoice = R.choice(aniKeys)
        this.animations.push(animationList[keyChoice]())

        if (R.bool()) {
            this.animations.push(animationList.orb())
        }
        this.animations.push(() => {
            this.camera.lookAt(new T.Vector3(0, 0, -200))
        })
        this.camera.position.x = R.weighted([1, 80 * R.negate(), 2, 0])
        const yOffset = R.weighted([1, 25 * R.negate(), 1, 0])

        // if (R.bool(0)) {
        // this.animations.push(createPongOrb(this, invisiShapesMaterial, geo))
        // } else {
        // this.animations.push(createAnimationMagician(this, invisiShapesMaterial))
        // }

        // Camera
        this.animations.push(f => {
            const div = 45000
            const div2 = 120000
            this.camera.position.z = Math.sin(((Date.now() % div) / div) * TAU) * 100
            this.camera.position.y = Math.sin(((Date.now() % div2) / div2) * TAU) * 50 + yOffset
        })
    }

    draw() {
        const period = 60000
        const period2 = 45000
        const t = Date.now()
        const phase = (t % period) / period
        const phase2 = (t % period2) / period2
        this.uniforms.u_phase.value = phase
        this.uniforms.u_phase2.value = phase2
        this.uniforms.u_now.value = t % 60000

        this.animations.forEach(a => a())

        this.renderer.render(this.scene, this.camera)
    }

    drawFrame() {
        requestAnimationFrame(this.draw.bind(this))
    }

    tick() {
        this.draw()
        requestAnimationFrame(this.tick.bind(this))
    }
}
