export function genTokenData(projectNum, hash) {
    let data = {}
    if (!hash) {
        hash = '0x'
        for (var i = 0; i < 64; i++) {
            hash += Math.floor(Math.random() * 16).toString(16)
        }
    }
    data.hash = hash
    data.tokenId = (projectNum * 1000000 + Math.floor(Math.random() * 1000)).toString()
    return data
}

export class Random {
    constructor(tokenData) {
        this.tokenData = tokenData
        this.compileHash()
    }
    compileHash() {
        this.useA = false
        let sfc32 = function (uint128Hex) {
            let a = parseInt(uint128Hex.substr(0, 8), 16)
            let b = parseInt(uint128Hex.substr(8, 8), 16)
            let c = parseInt(uint128Hex.substr(16, 8), 16)
            let d = parseInt(uint128Hex.substr(24, 8), 16)
            return function () {
                a |= 0
                b |= 0
                c |= 0
                d |= 0
                let t = (((a + b) | 0) + d) | 0
                d = (d + 1) | 0
                a = b ^ (b >>> 9)
                b = (c + (c << 3)) | 0
                c = (c << 21) | (c >>> 11)
                c = (c + t) | 0
                return (t >>> 0) / 4294967296
            }
        }
        this.prngA = new sfc32(this.tokenData.hash.substr(2, 32))
        this.prngB = new sfc32(this.tokenData.hash.substr(34, 32))
        for (let i = 0; i < 1e6; i += 2) {
            this.prngA()
            this.prngB()
        }
    }
    generateNewToken(hash) {
        const newToken = genTokenData(123, hash)
        this.tokenData.hash = newToken.hash
        this.tokenData.tokenId = newToken.tokenId
        this.compileHash()
    }
    random_dec() {
        this.useA = !this.useA
        return this.useA ? this.prngA() : this.prngB()
    }
    dec() {
        return this.random_dec()
    }
    num(a, b) {
        return a + (b - a) * this.dec()
    }
    int(a, b) {
        return Math.floor(this.num(a, b + 1))
    }
    bool(p = 0.5) {
        return this.dec() < p
    }
    choice(list) {
        return list[this.int(0, list.length - 1)]
    }
    negate(p = 0.5) {
        return this.dec() < p ? -1 : 1
    }
    weighted(list) {
        let weightSum = 0
        for (let i = 0; i < list.length; i += 2) {
            weightSum += list[i]
        }

        const r = this.dec() * weightSum

        let i = 0
        let acc = 0

        while (i < list.length) {
            acc += list[i]
            if (r < acc) {
                return list[i + 1]
            }
            i += 2
        }
    }
}
