import * as THREE from 'three'
import {vertexBasicShader} from './Shader'
import {createSmudgeFragShader} from './ShaderSmudge'

export function createSmudgeTexture(sketch) {
    const P = sketch.model.P
    const renderer = sketch.renderer
    const plane = new THREE.PlaneGeometry(1024, 1024)
    const camera = new THREE.PerspectiveCamera(1200, 1, 0.1, 200)
    const scene = new THREE.Scene()
    const dims = {width: sketch.sizes.width, height: sketch.sizes.height}
    // const dims = { width: 1000, height: 1000 }
    renderer.setSize(dims.width, dims.height)

    const R = sketch.random
    const uniforms = {
        u_resolution: {
            value: {
                x: dims.width,
                y: dims.height,
            },
        },
        u_random: {
            value: {x: R.dec(), y: R.dec(), z: R.dec()},
        },
    }

    const renderTarget = new THREE.WebGLRenderTarget(dims.width, dims.height)

    const material = new THREE.ShaderMaterial({
        vertexShader: vertexBasicShader,
        fragmentShader: createSmudgeFragShader(sketch),
        uniforms: uniforms,
    })

    const mesh = new THREE.Mesh(plane, material)
    mesh.position.z = -200
    scene.add(mesh)
    scene.add(camera)

    renderer.setSize(dims.width, dims.height)
    renderer.setRenderTarget(renderTarget)
    renderer.render(scene, camera)
    renderer.setRenderTarget(null)

    plane.dispose()
    material.dispose()

    return renderTarget.texture
}
