import {paletteDict} from './Palette'
import {forceFloat} from '../util'

function createRampPoints(colorRamp, nPoints) {
    const output = []
    output.push('RampPoint[MAX_POINTS] (')
    const blankRampPoints = 'RampPoint(2.0, vec3(0.0))'

    const ramps = []
    for (let i = 0; i < nPoints; i++) {
        if (i < colorRamp.length) {
            const data = colorRamp[i]
            let pos = data.position
            const c = data.color
            pos = forceFloat(pos)
            const r = forceFloat(c.r)
            const g = forceFloat(c.g)
            const b = forceFloat(c.b)

            let line = `\tRampPoint(${pos}, vec3(${r}, ${g}, ${b}))`
            ramps.push(line)
        } else {
            ramps.push(blankRampPoints)
        }
    }
    output.push(ramps.join(',\n'))

    output.push(')')
    return output.join('\n')
}

export function generateShaderPaletteGrid(parent, palette) {
    // FIX: Redo colors
    const R = parent.random
    const paletteList = [
        'mypal',
        'cdr',
        'prismatics',
        // 'prismaticfade',
        'pinkband',
        'purpleband',
        'blueglow',
        'greenglow',
        'pinkglow',
        'test2',
        'test8',
        // 'test7',
    ]
    let nPalettes = R.weighted([1, 1, 2, 2, 2, 3, 1, 4])
    // nPalettes = 11
    let palettes = []
    for (let i = 0; i < nPalettes; i++) {
      const p = paletteList.splice(R.num(0, paletteList.length), 1)
      palettes.push(paletteDict[p]())
    }

    // console.log(palettes)
    // palettes.push(paletteDict['mypal']())
    // palettes.push(paletteDict['prismatics']())
    // palettes.push(paletteDict['test7']())
    // cdr

    // Wrap
    palettes.push(palettes[0])

    const maxPoints = Math.max(
        ...palettes.map(o => {
            return o.length
        }),
    )
    const defines = `#define MAX_POINTS ${maxPoints}
#define N_PALETTES ${palettes.length}
`

    let paletteData = `
const Palette paletteData[N_PALETTES] = Palette[N_PALETTES] (`
    let paletteCode = []
    for (const p of palettes) {
        const nPoints = p.length
        const ramp = createRampPoints(p, maxPoints)
        paletteCode.push(`Palette(${nPoints}, ${ramp})`)
    }
    paletteData += paletteCode.join()
    paletteData += ');'

    const template = `
#ifdef GL_ES
precision mediump float;
#endif

${defines}
struct RampPoint {
  float position;
  vec3 color;
};

struct Palette {
  int nPoints;
  RampPoint points[MAX_POINTS];
};

${paletteData}

uniform vec2 u_resolution;
uniform float u_coef;

const vec3 D = vec3(95.047, 100.0, 108.883);

vec3 labToRgb(vec3 L) {
  vec3 v = vec3((L.x + 16.0) / 116.0);
  v.xz = vec2(L.y / 500.0 + v.y, v.y - L.z / 200.0);
  vec3 p = pow(v, vec3(3.0));
  v = mix((v - vec3(16.0 / 116.0)) / vec3(7.787), p, step(vec3(0.008856), p)) * D * 0.01;
  vec3 c = vec3(dot(v, vec3(3.2406, -1.5372, -0.4986)), dot(v, vec3(-0.9689, 1.8758, 0.0415)), dot(v, vec3(0.0557, -0.2040, 1.0570)));
  return mix(vec3(12.92) * c, vec3(1.055) * pow(c, vec3(1.0 / 2.4)) - vec3(0.055), step(vec3(0.0031308), c));
}

vec3 rgbToLab(vec3 v) {
  v = mix(v / vec3(12.92), pow(((v + vec3(0.055)) / vec3(1.055)), vec3(2.4)), step(vec3(0.04045), pow(((v + vec3(0.055)) / vec3(1.055)), vec3(2.4)))) * 100.0;
  v = vec3(dot(v, vec3(0.4124, 0.3576, 0.1805)), dot(v, vec3(0.2126, 0.7152, 0.0722)), dot(v, vec3(0.0193, 0.1192, 0.9505)));
  vec3 p = pow(v / D, vec3(1.0 / 3.0));
  v = mix((vec3(7.787) * v) + vec3((16.0 / 116.0)), p, step(vec3(0.008856), p));
  return vec3((116.0 * v.y) - 16.0, 500.0 * (v.x - v.y), 200.0 * (v.y - v.z));
}

vec3 getLab(float v, Palette pal) {
  int p = 0;
  for (int i = 0; i < pal.nPoints; i++) {
    if (v >= pal.points[i].position && v < pal.points[i + 1].position) {
      p = i;
      break;
    }
  }
  float p0 = pal.points[p].position;
  float p1 = pal.points[p + 1].position;
  return labToRgb(mix(rgbToLab(pal.points[p].color), rgbToLab(pal.points[p + 1].color), (v - p0) / (p1 - p0)));
}

vec3 processLab(vec2 v) {
  float yPos = v.y * float(N_PALETTES - 1);
  float yFract = fract(yPos);
  int yInt = int(yPos);
  Palette p0 = paletteData[yInt];
  Palette p1 = paletteData[yInt + 1];
  
  // How to mix palette
  float a = v.x;
  float b = a;

  if (true || (u_coef > 0.75)) {
    a = fract(v.x ${R.choice(['-', '+'])} v.y);
    b = a;
  }
    
  vec3 c0 = getLab(a, p0);
  vec3 c1 = getLab(b, p1);
  // vec3 c2 = mix(c0, c1, yFract);  // Original
  vec3 c2 = mix(c0, c1, 1.0 - (cos(yFract * ${Math.PI}) + 1.0) * 0.5);
  return c2;
}

void main() {
  vec2 st = gl_FragCoord.xy / u_resolution.xy;
  gl_FragColor = vec4(processLab(st), 1.0);
}`
    return template
}
